<template>
  <div>
    <!-- <mu-appbar style="width: 100%;" :color="$store.state.color" class="fiexdHeader backgroundNaviBar">
      <div slot="left" color="#3051f1">
        <a @click="()=>{
           $router.go(-1)
          }">
          <mu-button icon size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>
      <div style="width:100%;text-align:center;">
        <span style="font-size:18px;color:white;">运维列表</span>
      </div>
    </mu-appbar> -->
    <mu-appbar style="width: 100%;" class="header fiexdHeader backgroundNaviBar">
        <a @click="()=>{
          $router.go(-1)
        }">
          <mu-button icon slot="left" class="icon" size="20">
            <mu-icon value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
        <span style="color:white;font-size:18px;">运维列表</span>
      </mu-appbar>
    <div class="backWhite" style="margin-top:70px;">
      <div class v-for="(item,index) in list" :key="index">
        <div class="flexList">
          <div>
            <span>时间</span>
          </div>
          <div>
            <span>{{item.gmtOperate}}</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>监控站点</span>
          </div>
          <div>
            <span>{{item.station}}</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>类型</span>
          </div>
          <div>
            <span>{{item.type}}</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>维护人</span>
          </div>
          <div>
            <span>{{item.person}}</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>运维号</span>
          </div>
          <div>
            <span>{{item.operateId}}</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>备注</span>
          </div>
          <div>
            <span>{{item.remark}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="flex">
      <mu-expansion-panel
        :expand="panel === 'panel1'"
        @change="toggle('panel1')"
        style="width:96%;border-radius:10px;padding:0 10px;"
      >
        <div slot="header">其他内容</div>
        <div v-for="(item,index) in list" :key="index">
          <div class="flexList">
            <div>
              <span>设备名称</span>
            </div>
            <div>
              <span>{{item.content.deviceName}}</span>
            </div>
          </div>
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>故障原因</span>
            </div>
            <div class="flexListContent">
              <span>{{item.content.failureReason}}</span>
            </div>
          </div>
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>解决措施</span>
            </div>
            <div class="flexListContent">
              <span>{{item.content.solutions}}</span>
            </div>
          </div>
          <div v-for="(item,i) in item.content.replaceGroup" :key="i">
            <div class="flexList">
              <div>
                <span>更换内容</span>
              </div>
              <div>
                <span>{{item.replaceCnt}}</span>
              </div>
            </div>
            <div class="flexList">
              <div>
                <span>更换名称</span>
              </div>
              <div>
                <span>{{item.replaceNum}}</span>
              </div>
            </div>
          </div>
          <div class="flexList">
            <div>
              <span>规格型号</span>
            </div>
            <div>
              <span>{{item.content.modelType}}</span>
            </div>
          </div>
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>仪器标准</span>
            </div>
            <div class="flexListContent">
              <span>{{item.content.calibration}}</span>
            </div>
          </div>
          <!-- <div class="flexList">
            <div>
              <span>规格型号</span>
            </div>
            <div>
              <span>{{item.content.modelType}}</span>
            </div>
          </div>
          <div class="flexList">
            <div>
              <span>检修结果</span>
            </div>
            <div>
              <span>{{item.content.OverhaulRes}}</span>
            </div>
          </div>-->
        </div>
      </mu-expansion-panel>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
export default {
  name: "controlListDetails",
  data() {
    return {
      listContent: [],
      list: [],
      token: "",
      panel: ""
    };
  },
  methods: {
    toggle(panel) {
      this.panel = panel === this.panel ? "" : panel;
    },
    async initData() {
      // 定位
      // var  gmtOperate
      var url = "/job/operate/list";
      var pageNum = 1;
      var pageSize = 1000000000;
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageNum,
          pageSize
        },
        this.token
      );
      console.log(res);
      this.listContent = res.data.data.rows;
      for (let index = 0; index < this.listContent.length; index++) {
        const element = this.listContent[index];
        this.queryId(element);
      }
    },
    queryId(e) {
      var id = this.$route.query.name;

      if (e.operateId === id) {
        return this.list.push(e);
      }
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    this.initData();
  }
};
</script>
<style scoped>
.flexList {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
  border-bottom: 1px rgb(241, 240, 240) solid;
}
.backWhite {
  background: white;
  margin: 5px;
  border-radius: 10px;
}
.flex {
  display: flex;
  justify-content: center;
}
.flexListContent {
  width: 100%;
  height: 100px;
  border-radius: 10px;
  border: #ccc 1px solid;
  padding: 5px;
  margin-bottom: 5px;
  /* align-items: center;
  align-content: center; */
}
.fiexdHeader {
  position: fixed;
  top: 0;
}
</style>



